@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.content .projects {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
}

.content .projects .card {
  width: calc(50% - 5rem);
  background: rgba(185, 185, 185, 0.3);
  border-radius: 0.5rem;
  padding: 1rem;
  margin: 1.5rem 1rem;
}

.content .projects .card a, .content .projects .card a:visited, .content .projects .card a:focus {
  color: #000;
}

.content .projects .card a {
  text-decoration: none;
}

.content .projects .card a h1 {
  font-size: 1.3rem;
  font-weight: bold;
}

.content .projects .card p {
  font-size: 1rem;
}

.contact {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.contact span {
  font-weight: bold;
  margin-bottom: 1.5rem;
}

.contact span:not(:first-child) {
  margin-top: 1rem;
}

.contact .my-contents {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact .my-contents .twitch-area a,
.contact .my-contents .youtube-area a {
  text-decoration: none;
  color: #000;
}

.contact .my-contents .twitch-area,
.contact .my-contents .youtube-area {
  width: 9rem;
  padding: .5rem 0;
  margin: 0 1.5rem;
  border-radius: .5rem;
}

.contact .my-contents .twitch-area {
  background-color: rgba(90, 62, 133, 0.3);
}

.contact .my-contents .youtube-area {
  background-color: rgba(238, 49, 36, 0.3);
}

.contact .my-contents > div > a h3 {
  font-weight: 700;
  font-size: 1.5rem;
}

.contact .my-contents > div > a h4 {
  font-size: 1.1rem;
}

.contact .my-contents > div > a img {
  margin-top: .5rem;
}

.icon {
  color: #000;
  transition: color 0.4s ease;
  margin: 0 .25rem;
}

.icon:hover {
  color: gray;
}

.icons {
  background-color: #72727250;
  padding: 1rem;
  border-radius: 0.8rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.icons a {
  text-decoration: none;
}

.icons :not(:first-child) {
  margin-left: 1rem;
}

html, body {
  font: 1rem 'Poppins', sans-serif;
  overflow-x: hidden;
}

.container {
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  /* Background */
  background-color: #f1f1f1;
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23c0bfc1' fill-opacity='0.2' fill-rule='evenodd'%3E%3Ccircle cx='3' cy='3' r='3'/%3E%3Ccircle cx='13' cy='13' r='3'/%3E%3C/g%3E%3C/svg%3E");
}

.container header,
.container footer {
  flex-shrink: 0;
}

.container header {
  display: flex;
  background-color: #f1f1f1;
  padding: 2rem 5rem;
  font-size: 2rem;
  transition: color 0.2s ease;
}

.container header .logo {
  line-height: 1;
  display: inline-flex;
  cursor: pointer;
  align-items: center;
}

.container header .logo:hover {
  color: #5D3FD3;
}

.container header .logo span {
  font-weight: bold;
}

.container header .fa-code {
  margin-right: 0.8rem;
  font-size: 1.6rem;
}

.container nav {
  background-color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  box-shadow: 10px 10px 20px 0px rgba(194, 194, 194, 0.75);
}

.container nav a {
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 1.5rem .5rem;
  text-decoration: none;
  transition: color, background-color 0.3s ease-in;
  font-family: 'Quicksand', sans-serif;
  font-weight: bold;
}

.container nav a:not(:first-child) {
  margin-left: 2rem;
}

.container nav a:hover {
  color: #b1b1b1;
}

.container .content {
  flex-grow: 1;
  margin: 1.6rem 2rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 1.6rem;
}

.container .content #count {
  font-weight: bold;
}

.container footer {
  background-color: #f1f1f1;
  text-align: center;
  padding: 1rem;
  box-shadow: 0px -5px 30px 0px rgba(97, 97, 97, 0.25);
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: var(--bg-color);
}

::-webkit-scrollbar-thumb {
  background: #888;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Typed.JS */
.text-popup {
  width: 22rem;
  height: 3.2rem;
}

.text-popup .text {
  font-size: 2.2rem;
  font-weight: bold;
}

/* Media Queries */
@media (max-width: 600px) {
  .container .content {
    margin: 1rem;
  }
  .container .content .textPopup .text {
    font-size: 1.6rem !important;
  }
  .top-nav a {
    font-size: 0.8rem;
    padding: 1.2rem 0.3rem;
  }
  .top-nav a:not(:first-child) {
    margin-left: 0.4rem;
  }
  .header {
    align-items: center;
    justify-content: center;
  }
  /* Other Pages */
  .content .projects .card {
    width: 100%;
    padding: 0.6rem;
    margin: 0.75rem 0.5rem;
  }
}

@media (max-width: 370px) {
  .header a {
    font-size: 1.4rem;
  }
}
